import React from "react";

export default function Table() {
	return (
		<table className="text-left w-full md:w-[65%] lg:w-[45%] border-[1px] border-[#979797] w-[45%] font-sans mt-6 md:mt-24 lg:mt-10 lg:ml-24">
				<tr><th>OS:</th><th>Processor:</th></tr>
				<tr className="border-b-[1px] border-[#979797]"><td>Windows 7 64-bit only(No OSX support at this time)</td><td>Intel Core 2 Duo @ 2.4 GHZ or AMD Athlon X2 @ 2.8 GHZ</td></tr>
				<tr><th>Memory:</th><th>Storage:</th></tr>
				<tr className="border-b-[1px] border-[#979797]"><td>8 GB RAM</td><td>8 GB available space</td></tr>
				<tr><th colSpan={2}>Graphics:</th></tr>
				<tr><td colSpan={2}>NVIDIA GeForce GTX 660 2GB or AMD Radeon HD 7850 2GB DirectX11 (Shader Model 5)</td></tr>
			</table>
	)
}