import React from "react";
import MyForm from "./MyForm";

export default function Subscription() {
	return (
		<section id="sub" className="flex flex-col nm:px-8 lg:px-0 h-full py-24 px-6 mdm:py-16 lg:py-12 bg-black">
			<div className="flex flex-row lg:justify-around items-center">
			<img className="-left-[20%] relative w-7/12 lg:w-fit lg:static" src={require(`../../assets/images/skull.png`)} alt=""/>
			<div className="flex flex-col -ml-[5rem] lg:-ml-[6.5rem] w-7/12 lg:w-6/12 relative">
				<h2 className="font-title whitespace-nowrap tracking-[2px] text-xl sm:text-2xl">Want to stay in touch?</h2>
				<h1 className="font-title tracking-wider text-5xl md:text-6xl lg:text-7xl mt-1">newsletter subscribe</h1>
				<div className="text-justify text-base md:text-lg leading-6 my-5 lg:leading-7 lg:my-10 lg:pr-40">
				In order to start receiving our news, all you have to do is enter your email address. 
				Everything else will be taken care of by us. We will send you emails containing information about game. We dont spam.
				</div>
				<MyForm display="hidden nm:flex"/>
			</div>
			</div>
			<MyForm display="flex nm:hidden"/>
		</section>
	)
}