import React from "react";
import FooterNav from "./FooterNav";
import Social from "./Social"

export default function Footer() {
	const aClass = "cursor-pointer hover:underline"
	return (
		<footer className="flex flex-col h-fit px-6 pb-6 bg-black">
			<div className="flex flex-col lg:flex-row justify-between">
				<img className="w-16 h-16 self-center" src={require("../../assets/icons/logo.png")} alt=""/>
				<FooterNav/>
				<div className="self-center mt-4"><Social/></div>
			</div>
			<hr className="my-4 opacity-50"/>
			<div className="flex flex-col lg:flex-row justify-between text-sm">
				<span className="opacity-50 self-center"> 2018 Outpost Games, Inc. All Rights Reserved</span>
				<div className="flex flex-col items-center gap-4 lg:gap-2 mt-4 lg:mt-0 lg:flex-row uppercase font-bold text-xs tracking-wider">
					<a className={aClass} href="https://en.wikipedia.org/wiki/Privacy_policy" rel="noreferrer" target="_blank">Privacy Policy</a>
					<a className={aClass} href="https://en.wikipedia.org/wiki/Terms_of_service" rel="noreferrer" target="_blank">| Terms of Services |</a>
					<a className={aClass} href="https://en.wikipedia.org/wiki/Code_of_conduct" rel="noreferrer" target="_blank">Code of Conduct</a>
					</div>
				</div>
		</footer>
	)
}