import React, {useState} from "react";
import { useDispatch } from "react-redux";
import { setBurgerState } from "../../../store/burgerSlice";
import { useSelector } from "react-redux";
import { lengthChanger } from "../animation/lengthChanger";
import Nav from './Nav';

export default function Header () {
	const dispatch = useDispatch();
	const { burgerState } = useSelector(state => state.burger);
	const [langText, setLangText] = useState('Eng ')
	const [invisibleLangContainer, setInvisibleLangContainer] = useState('invisible')
	const [eventState, setEventState] = useState(null)
	const langContainer = `flex items-center mt-2 bg-black opacity-75 rounded-md p-2 uppercase flex-col ${invisibleLangContainer} text-xs xs:text-sm`
	
	function changeLangText () {
		langText === 'Eng ' ? setLangText('Eng ') : setLangText('Eng ')
		invisibleLangContainer === 'invisible' ? setInvisibleLangContainer('visible') : setInvisibleLangContainer('invisible')
	};

	function handleBurgerClick (event) {
		lengthChanger(event, burgerState, dispatch, setBurgerState)
		setEventState(event.currentTarget)
	};
	
	return (
		<header className="w-full justify-between mx-auto h-20 flex font-sans font-semibold">		
			<img className="w-16 h-16" src={require("../../../assets/icons/logo.png")} alt=""/>
				<Nav eventState={eventState}/>
			<div className="space-x-6 flex">
			<div className="mt-7">
				<span onClick={() => changeLangText(langText)} className="self-center uppercase cursor-pointer items-center text-sm xs:text-base lg:text-sm">{langText}</span>
				<div className={langContainer}>
					<span className="cursor-pointer hover:text-[#FFB548]">Rus</span>
					<span className="cursor-pointer hover:text-[#FFB548]">Fra</span>
					<span className="cursor-pointer hover:text-[#FFB548]">Deu</span>
				</div>
			</div>
			<div className="bg-white opacity-50 mx-10 rounded-md h-4/6 self-center w-[2px]"></div>
				<div id="burger_links" className="justify-between items-center space-x-5 hidden lg:flex">					
					<a href="https://store.steampowered.com/" target="_blank" rel="noreferrer"><svg className="w-[40px] h-[60px] xl:h-[50] hover:opacity-80" viewBox="0 0 31 31" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path fillRule="evenodd" clipRule="evenodd" d="M15.7746 30.5089C24.0936 30.5089 30.8375 23.7932 30.8375 15.5089C30.8375 7.22461 24.0936 0.508881 15.7746 0.508881C7.45558 0.508881 0.71167 7.22461 0.71167 15.5089C0.71167 17.0369 0.941106 18.5116 1.36754 19.9005C1.47636 19.9201 1.6523 19.9646 1.90866 20.0395L1.50834 13.7669C1.50834 13.6519 1.7031 13.5966 1.88153 13.546C1.97946 13.5182 2.07247 13.4918 2.12567 13.4577C2.27094 13.3662 5.26258 14.454 5.42239 14.5262L9.248 16.2495C10.2359 15.6285 11.4175 15.3782 12.5797 15.5467L16.0566 11.3252C16.1002 7.95572 18.8604 5.24088 22.2551 5.23606C25.4269 5.26976 28.0564 7.67653 28.3567 10.8101C28.6763 14.1989 26.1775 17.2025 22.7684 17.525L16.9767 21.6406C16.6232 23.4457 15.3254 24.9235 13.5724 25.5107C10.8848 26.4157 7.96472 24.9812 7.05917 22.3049L5.04467 21.6406C4.89531 21.5934 2.15386 20.4576 1.41129 20.0406C3.34051 26.1099 9.04168 30.5089 15.7746 30.5089ZM25.3546 11.3973C25.3546 13.0987 23.967 14.478 22.2553 14.478C20.5436 14.478 19.156 13.0987 19.156 11.3973C19.156 9.69591 20.5436 8.31664 22.2553 8.31664C23.967 8.31664 25.3546 9.69591 25.3546 11.3973ZM24.3236 11.3973C24.3236 12.5325 23.3978 13.4527 22.2557 13.4527C21.1137 13.4527 20.1879 12.5325 20.1879 11.3973C20.1879 10.2622 21.1137 9.34195 22.2557 9.34195C23.3978 9.34195 24.3236 10.2622 24.3236 11.3973ZM10.5804 23.412C10.9969 23.6142 11.4569 23.7201 11.9218 23.7201C12.2753 23.7201 12.6191 23.6623 12.9581 23.5516C13.7377 23.282 14.3721 22.7189 14.7304 21.9824C15.4761 20.4469 14.8272 18.6081 13.2873 17.8668C13.0259 17.7417 12.7208 17.8476 12.5949 18.1027C12.469 18.3626 12.5755 18.6659 12.8322 18.791C12.8661 18.8055 12.9 18.8199 12.9338 18.8392C13.9508 19.359 14.343 20.5961 13.82 21.6069C13.297 22.6178 12.0477 23.0077 11.0356 22.4878C10.7741 22.3627 10.469 22.4686 10.3431 22.7237C10.2172 22.9836 10.3238 23.2869 10.5804 23.412Z" fill="#FFB548"/>
					</svg></a>
					<a href="https://www.xbox.com/ru-RU/" target="_blank" rel="noreferrer"><svg className="w-[40px] h-[60px] xl:h-[50] hover:opacity-80" viewBox="0 0 31 31" fill="none" xmlns="http://www.w3.org/2000/svg">
						<path d="M10.5514 3.15842C12.1356 3.77963 13.667 4.6001 15.1104 5.60225C15.2747 5.7136 15.4625 5.77221 15.6502 5.77221C15.838 5.77221 16.0257 5.7136 16.19 5.60225C17.6334 4.6001 19.1648 3.77377 20.7491 3.15256C21.107 3.0119 21.3476 2.66027 21.3417 2.27348C21.3417 1.88668 21.1011 1.54091 20.7373 1.40612C17.5689 0.204714 13.7315 0.204714 10.569 1.40612C10.211 1.54091 9.9646 1.89254 9.9646 2.27348C9.9646 2.65441 10.1876 3.01776 10.5514 3.15842Z" fill="#FFB548"/>
						<path d="M24.1587 3.16374C23.8594 2.95862 23.478 2.94104 23.1553 3.12271C21.4009 4.13072 19.7052 5.28524 18.1034 6.55698C17.8922 6.72693 17.7631 6.97893 17.7513 7.24852C17.7396 7.5181 17.8452 7.78183 18.0388 7.96936C21.3599 11.1282 25.96 20.8156 27.0162 25.2696C24.7278 21.4017 19.6231 12.3003 16.2845 9.18835C15.9265 8.8543 15.3633 8.8543 15.0053 9.18835C11.6608 12.3003 6.5737 21.4075 4.28536 25.2813C5.34152 20.8273 9.92992 11.1282 13.2509 7.96936C13.4504 7.78183 13.5561 7.5181 13.5443 7.24852C13.5326 6.97893 13.4035 6.72693 13.1923 6.55698C11.5904 5.28524 9.88885 4.13072 8.14033 3.12271C7.82935 2.94104 7.43622 2.95862 7.13698 3.16374C3.05905 5.97093 0.624023 10.5832 0.624023 15.506C0.624023 19.1043 1.92075 22.5738 4.28536 25.2813C7.11351 28.5984 11.2501 30.5089 15.6449 30.5089C20.0397 30.5089 24.1822 28.6042 27.0162 25.2755C29.369 22.5796 30.6658 19.1102 30.6658 15.506C30.6658 10.5832 28.2307 5.97093 24.1587 3.16374Z" fill="#FFB548"/>
					</svg></a>
				</div>
				<svg onClick={(event) => {handleBurgerClick(event)}} className="block lg:hidden self-center z-20" width="25" height="25" viewBox="0 0 25 18" fill="none" xmlns="http://www.w3.org/2000/svg">
					<rect className="transition-all" x="9.49854" y="15.0166" width="23.4873" height="2.5" fill="white"/>
					<rect className="transition-all" x="1.596191" y="7.51657" width="23.4873" height="2.5" fill="white"/>
					<rect className="transition-all" x="1.596191" y="0.016571" width="23.4873" height="2.5" fill="white"/>
				</svg>						
			</div>
		</header>
	)
}