import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, A11y } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';

export default function Slider () {
	return (
		<Swiper
		modules={[Navigation, Pagination, A11y]}
		spaceBetween={50}
		slidesPerView={1}
		navigation
		pagination={{ clickable: true }}
		className="lg:absolute w-full sm:w-9/12 md:w-8/12 lg:10/12 xs:-mt-9 sm:-mt-10 lg:mt-0 self-center lg:w-11/12">
				<SwiperSlide className="!scale-[.65] lg:!scale-80">
					<img src={require('../../assets/images/image1.png')} className="rounded-md" alt="image1"/>
				</SwiperSlide>
				<SwiperSlide className="!scale-[.65] lg:!scale-80">	
					<img src={require('../../assets/images/image2.png')} className="rounded-md" alt="image2"/>
				</SwiperSlide>
				<SwiperSlide className="!scale-[.65] lg:!scale-80">
					<img src={require('../../assets/images/image3.png')} className="rounded-md" alt="image3"/>
				</SwiperSlide>
			</Swiper>
	);
};