import React from "react";
import { Link } from "react-scroll";

export default function StoryBtn () {
	return (
		<Link to="about" spy={true} smooth={true} offset={50} duration={500} href="#about" id="story_btn" className="flex mt-auto mb-[2.2rem] self-center flex-col w-fit text-xs xs:text-base items-center uppercase text-sm font-bold font-sans cursor-pointer">
		The story
		<svg className="mt-2 animate-bounce" xmlns="http://www.w3.org/2000/svg" width="23" height="11" viewBox="0 0 23 11" fill="none">
			<path d="M21.5786 1.83362H1.32422L11.338 10.0027L21.5786 1.83362Z" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
		</svg>
	</Link>
	)
}