import React from "react";
import { Link } from "react-scroll";

export default function FooterNav() {
	const linkClass = "uppercase hover:underline my-4 mdm:my-0 cursor-pointer font-bold text-xs tracking-wider"
	return (
		<nav id="burger_nav" className="flex flex-wrap mt-4 lg:mt-0 w-full lg:px-20 items-center justify-around lg:justify-between">
			<Link className={linkClass} to="about">About</Link>
			<Link className={linkClass} to="features">Game Features</Link>
			<Link className={linkClass} to="req">System requirements</Link>
			<Link className={linkClass} to="quotes">Quotes</Link>
			<Link className={linkClass} to="sub">Subscription</Link>
		</nav>
	)
}