export function lengthChanger(event, state, dispatch, funcState) {
	const lengthDefiner = (event, i, length) => {
		event.currentTarget ? event.currentTarget.children[i].x.baseVal.value = length : event.children[i].x.baseVal.value = length
	}
	if (window.innerWidth < 1024) {
		if (!state) {
			lengthDefiner(event, 0, 3.596191)
			lengthDefiner(event, 1, 9.17235)
			lengthDefiner(event, 2, 14.49854)
			dispatch(funcState('active'));
			document.body.style = 'overflow: hidden'
		}
		else {
			lengthDefiner(event, 0, 9.49854)
			lengthDefiner(event, 1, 1.596191)
			lengthDefiner(event, 2, 1.596191)
			dispatch(funcState(''));
			document.body.style = ''
		}	
	}
}