import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from "yup";

const validationSchema = Yup.object({
  email: Yup.string().email().required(),
});

export default function MyForm({display}) {
  const [formSubmitted, setFormSubmitted] = useState(false);

  const formik = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      console.log("Form submitted with values:", values);
      setFormSubmitted(true);
    },
  });

  return (
    <form
      onSubmit={formik.handleSubmit}
      className={`flex-col lg:flex-row w-full lg:w-[80%] items-center lg:mt-0 relative ${display}`}>
      <label className="absolute self-start opacity-50 ml-2.5 mt-1">Your email address</label>
      <input
        type="text"
        id="email"
        name="email"
        onChange={formik.handleChange}
        onBlur={formik.handleBlur}
        value={formik.values.email}
        className={`container flex bg-input px-3 pt-6 pb-2 w-full outline-none outline-none ${
          formik.errors.email ? "outline-red-500" : "focus:outline-[#FFB548]"
        } text-[#FFB548] font-sans rounded-md`}/>
      <button
        type="submit"
        className="container flex w-full lg:w-fit justify-center whitespace-nowrap shadow-sm py-4 mt-4 lg:mt-0 lg:ml-4 lg:px-6 xl:px-7 rounded-md font-bold bg-gradient-to-r from-[#FFB548] 
        from-11.27% to-[#F3AF34] to-90.4% text-black font-sans text-xs xs:text-base transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-105 hover:shadow-lg duration-300">
        {formSubmitted ? "Thank you!" : "Subscribe now"}
      </button>
    </form>
  );
};
