import { createSlice } from "@reduxjs/toolkit";

const initialState = {
	burgerState: '',
};

export const burgerSlice = createSlice({
	name: "burger",
	initialState,
	reducers: {
		setBurgerState: (state, action) => {
			state.burgerState = action.payload;
		},
	},
});

export const {
	setBurgerState,
} = burgerSlice.actions;

export default burgerSlice.reducer;
