import React from "react";
import Header from "./Header/Header";
import StoryBtn from "./StoryBtn";

export default function Main({ onBackgroundLoad }) {
  const handleBackgroundLoad = () => {
    if (onBackgroundLoad) {
      onBackgroundLoad();
    }
  };

  return (
    <section
      id="main"
      className="flex flex-col p-3 lg:p-5 h-screen bg-main-image bg-cover bg-no-repeat bg-bottom"
      onLoad={handleBackgroundLoad}
    >
      <Header />
      <main className="w-full flex flex-col justify-center items-center mt-auto mb-20 text-center font-title">
        <h1 className="text-[2.5rem] leading-tight text-5xl md:text-6xl lg:text-7xl tracking-wider">
          Survive at all costs
        </h1>
        <h2 className="text-[0.5rem] xs:text-base sm:text-xl md:text-2xl tracking-[2px]">
          Experience new social battle royale game
        </h2>
        <button className="container whitespace-nowrap flex w-fit self-center shadow-sm flex-row py-4 px-7 rounded-md font-bold mt-3 lg:mt-5 bg-gradient-to-r from-[#FFB548] from-11.27% to-[#F3AF34] to-90.4% text-black font-sans text-xs xs:text-base transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-105 hover:shadow-lg duration-300">
          Buy now on Steam
          <div className="mx-2.5 bg-black w-[2px] opacity-50 h-full"></div> $14.99
        </button>
      </main>
      <StoryBtn />
    </section>
  );
}
