import React from "react";

export default function Icon({icon}) {
	return (
		<div>
			<img src={require(`../../assets/icons/${icon}.png`)} className="relative w-12 lg:w-16 rounded-full z-10" alt=""/>
			<svg className="relative -mt-[2.9rem] lg:-mt-[3.8rem] ml-2 z-0 w-12 lg:w-16" viewBox="0 0 65 65" fill="none" xmlns="http://www.w3.org/2000/svg">
			<circle cx="32.3873" cy="32.5696" r="32.1531" fill="#FFB548"/>
			</svg>
		</div>
	)
}