import React from "react";
import QuotBlock from "./QuotBlock";

export default function Quotes() {
	return (
		<section id="quotes" className="flex flex-col items-center lg:flex-row p-6 lg:px-24 h-full sm:h-screen bg-quotes-image bg-cover bg-center bg-no-repeat">
			<div className="flex flex-col">
				<h2 className="font-title tracking-[2px] text-2xl">What people think?</h2>
				<h1 className="font-title tracking-wider text-5xl md:text-6xl lg:text-7xl mt-1">Press quotes</h1>
				<div className="font-sans text-justify text-base md:text-lg my-4 leading-6 lg:leading-7 lg:my-8 lg:pr-[25%]">
					Our goal is to create a product and service that youre satisfied with and use it every day. This is why were constantly working on our services to make it better every day and really listen to what our users has to say.
				</div>
				<button className="container whitespace-nowrap flex w-fit shadow-sm py-4 px-7 rounded-md font-bold mt-3 lg:mt-5 bg-gradient-to-r from-[#FFB548] 
				from-11.27% to-[#F3AF34] to-90.4% text-black font-sans text-xs xs:text-base transition ease-in-out delay-150 hover:-translate-y-1 hover:scale-105 hover:shadow-lg duration-300">
					Read more testimonials
				</button>
			</div>
			<div className="flex flex-col w-[150%] gap-0 lg:gap-6">
				<QuotBlock
				 name="Evan Lahti"
				 job="PC Gamer"
				 text="One of my gaming highlights of the year."
				 date="October 18, 2018"
				 icon="chel1"
				 margin=""
				 />
				 <QuotBlock
				 name="Jada Griffin "
				 job="Nerdreactor"
				 text="The next big thing in the world of streaming and survival games."
				 date="December 21, 2018"
				 icon="chel2"
				 margin="mr-36"
				 />
				 <QuotBlock
				 name="Aaron Williams"
				 job="Uproxx"
				 text="Snoop Dogg Playing The Wildly Entertaining SOS Is Ridiculous."
				 date="December 24, 2018"
				 icon="chel3"
				 margin="mr-9"
				 />
			</div>
		</section>
	)
}